<template>
  <div class="" style="margin:20px 0 0 20px">
    <div class="flex sub-box">
      <div class="check-box">
        <el-form label-width='140px'>
          <p><span class="sub-title">1</span> 输入基础信息</p>
          <el-form-item label='计算模式：'>
            <el-select v-model.trim="submitData.checkMode" class="wid-100" @change="setClear">
              <el-option v-for="item in checkModeList" :key="item.value" :label='item.label' :value='item.value'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label='汇率：'>
            1 CNY ≈ {{submitData.exchange}} TWD
            <el-button @click="exchangeDialog=true">汇率设置</el-button>
          </el-form-item>
          <el-form-item label='展示方式：'>
            <el-select v-model.trim="submitData.showMode" class="wid-100" @change="setClear">
              <el-option v-for="item in showModeList" :key="item.value" :label='item.label' :value='item.value'></el-option>
            </el-select>
          </el-form-item>
          <p v-if="submitData.showMode===0"><span class="sub-title">2</span> 输入区间商品信息</p>
          <p v-if="submitData.showMode===1"><span class="sub-title">2</span> 输入单个商品信息</p>
          <el-form-item label='商品成本最大值：' v-if="submitData.showMode===1">
            <el-input placeholder="0.00" v-model.number.trim="submitData.maxCost">
              <span slot="prefix">￥</span>
              <span slot="suffix">CNY</span>
            </el-input>
          </el-form-item>
          <el-form-item label='商品成本最小值：' v-if="submitData.showMode===1">
            <el-input placeholder="0.00" v-model.number.trim="submitData.minCost">
              <span slot="prefix">￥</span>
              <span slot="suffix">CNY</span>
            </el-input>
          </el-form-item>
          <el-form-item label='间隔：' v-if="submitData.showMode===1">
            <el-input placeholder="0.00" v-model.number.trim="submitData.step">
              <span slot="prefix">￥</span>
              <span slot="suffix">CNY</span>
            </el-input>
          </el-form-item>
          <el-form-item label='商品成本价：' v-if="submitData.showMode===0">
            <el-input placeholder="0.00" v-model.number.trim="submitData.cost">
              <span slot="prefix">￥</span>
              <span slot="suffix">CNY</span>
            </el-input>
          </el-form-item>
          <el-form-item label='预期净利润：' v-if="submitData.checkMode===0">
            <el-input placeholder="0.00" v-model.number.trim="submitData.clearProfit">
              <span slot="prefix">￥</span>
              <span slot="suffix">CNY</span>
            </el-input>
          </el-form-item>
          <el-form-item label='预期净利润率：' v-if="submitData.checkMode===1">
            <el-input placeholder="0.00" v-model.number.trim="submitData.profit">
              <span slot="suffix">%</span>
            </el-input>
          </el-form-item>

          <el-form-item label='折扣：'>
            <el-input placeholder="0.00" v-model.number.trim="submitData.discount">
              <span slot="suffix">%OFF</span>
            </el-input>
          </el-form-item>
          <el-form-item label='包裹重量：'>
            <el-input placeholder="0.00" v-model.number.trim="submitData.weight" disabled>
              <span slot="suffix">g</span>
            </el-input>
          </el-form-item>
          <p><span class="sub-title">3</span> 输入其他信息</p>
          <el-form-item label='境内段运费：'>
            <el-input placeholder="0.00" v-model.number.trim="submitData.freight">
              <span slot="prefix">￥</span>
              <span slot="suffix">CNY</span>
            </el-input>
          </el-form-item>
          <el-form-item label='佣金费率：'>
            <el-radio v-model.trim="submitData.commission" :label="6">6%</el-radio>
            <el-radio v-model.trim="submitData.commission" :label="5.5">5.5%</el-radio>
            <el-radio v-model.trim="submitData.commission" :label="5">5%</el-radio>
            <el-radio v-model.trim="submitData.commission" :label="0">免佣期</el-radio>
          </el-form-item>
          <el-form-item label='交易手续费费率：'>
            <el-input placeholder="0.00" v-model.number.trim="submitData.service">
              <span slot="suffix">%</span>
            </el-input>
          </el-form-item>
          <el-form-item label='活动服务费费率：'>
            <el-input placeholder="0.00" v-model.number.trim="submitData.activity">
              <span slot="suffix">%</span>
            </el-input>
          </el-form-item>
          <el-form-item label='提现手续费费率：'>
            <el-input placeholder="0.00" v-model.number.trim="submitData.withdraw">
              <span slot="suffix">%</span>
            </el-input>
          </el-form-item>
          <el-row>
            <el-col>
              <el-button type="success" @click="openUrl()">官方工具</el-button>
              <el-button type="danger" @click="clickFn()"><i class="el-icon-edit el-icon--left"></i>计算</el-button>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <div class="check-box banner" style="margin-left:20px;height:691px;overflow:auto">
        <div style="border:1px solid #E5E5E5">
          <p style="background-color:#F6F6F6;padding:5px 20px">参考结果</p>
        </div>
        <div v-if="submitData.showMode===1">
          <div class="flex" style="border-bottom:1px solid #E5E5E5;padding:10px">
            <div style="width:70px;text-align:center">商品成本</div>
            <div style="width:120px;text-align:center">折前售价 TWD</div>
            <div style="width:120px;text-align:center">商品售价 TWD</div>
          </div>
          <div>
            <div class="flex" style="border-bottom:1px solid #E5E5E5;padding:10px;" v-for="item in forArr" :key="item.name">
              <div style="width:70px;text-align:center">
                <span style="background-color:#C70505;color:#fff;padding:0 5px">{{item.name}}元</span>
              </div>
              <div style="width:120px;text-align:center">{{item.beforePrice}}</div>
              <div style="width:120px;text-align:center">{{item.afterPrice}}</div>
            </div>
          </div>

        </div>
        <div v-if="submitData.showMode===0">
          <el-row style="border-bottom:1px solid #E5E5E5;padding:10px">
            <el-col :span='12'>折前售价</el-col>
            <el-col :span='12' v-if="reultData.beforePrice">{{(reultData.beforePrice-0).toFixed(2)}} TWD <span style="color:#999;font-size:12px">≈ {{(reultData.beforePrice/submitData.exchange).toFixed(2)}}CNY</span></el-col>
          </el-row>
          <el-row style="border-bottom:1px solid #E5E5E5;padding:10px">
            <el-col :span='12'>商品售价（折后售价）</el-col>
            <el-col :span='12' v-if="reultData.afterPrice">{{(reultData.afterPrice-0).toFixed(2)}} TWD <span style="color:#999;font-size:12px">≈ {{(reultData.afterPrice/submitData.exchange).toFixed(2)}}CNY</span></el-col>
          </el-row>
          <el-row style="border-bottom:1px solid #E5E5E5;padding:10px" v-if="submitData.checkMode===0">
            <el-col :span='12'>净利润率</el-col>
            <el-col :span='12' v-if="reultData.resClearProfits">{{(reultData.resClearProfits-0).toFixed(2)}}%</el-col>
          </el-row>
          <el-row style="border-bottom:1px solid #E5E5E5;padding:10px" v-if="submitData.checkMode===1">
            <el-col :span='12'>净利润</el-col>
            <el-col :span='12' v-if="reultData.resProfits">{{(reultData.resProfits-0).toFixed(2)}} TWD <span style="color:#999;font-size:12px">≈ {{((reultData.resProfits)/submitData.exchange).toFixed(2)}}CNY</span></el-col>
          </el-row>
          <el-row style="border-bottom:1px solid #E5E5E5;padding:10px">
            <el-col :span='12'>订单收入</el-col>
            <el-col :span='12' v-if="reultData.income">{{(reultData.income-0).toFixed(2)}} TWD <span style="color:#999;font-size:12px">≈ {{(reultData.income/submitData.exchange).toFixed(2)}}CNY</span></el-col>
          </el-row>
        </div>
      </div>
      <el-dialog title="汇率设置" :visible.sync="exchangeDialog" width="25%" :close-on-click-modal='false'>
        <div>
          <el-input v-model.trim="submitData.exchange" oninput="value= value.match(/(\d+)?(\.)?([0-9]{1,2})?/) ? value.match(/(\d+)?(\.)?([0-9]{1,2})?/)[0] : ''"></el-input>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="exchangeDialog = false">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>

</template>

<script>
export default {
  computed: {},
  components: {},
  props: {},
  data() {
    return {
      submitData: {
        checkMode: 0,//计算方式
        exchange: 4.36,//汇率
        showMode: 0,//展示模式
        minCost: 15,
        maxCost: 50,
        cost: 30,//成本
        step: 1,//间隔
        clearProfit: "",//预期净利润
        profit: 20,//利润率
        discount: 34,//折扣
        weight: 500,//包裹重量
        freight: 5,//境内运费
        commission: 0,//佣金费率
        service: 2,//服务费
        activity: '',//活动费
        withdraw: ''//提现手续费
      },
      checkModeList: [
        {
          label: '通过预期净利润,计算售价和净利润',
          value: 0
        },
        {
          label: '通过预期净利润率,计算售价和净利润',
          value: 1
        }
      ],
      showModeList: [
        {
          label: '展示单个商品价格',
          value: 0
        },
        {
          label: '展示区间价格',
          value: 1
        },
      ],
      exchangeDialog: false,
      reultData: {
        beforePrice: '',//折前售价
        afterPrice: '',//折后售价
        resProfits: '',//净利润
        resClearProfits: "",//利润率
        income: ''//订单收入
      },
      forArr: []
    }
  },
  created() {

  },
  mounted() {
    if (localStorage.getItem("shopeData")) {
      this.submitData = JSON.parse(localStorage.getItem("shopeData"))
    }
  },
  methods: {
    setClear() {
      this.reultData = {
        beforePrice: '',//折前售价
        afterPrice: '',//折后售价
        resProfits: '',//净利润
        resClearProfits: "",//利润率
        income: ''//订单收入
      }
    },
    openUrl() {
      const tmpUrl = 'https://solutions.shopee.cn/sellers/pricing-simulator/#/'
      window.open(tmpUrl)
    },
    openExchangeDialog() {
      this.exchangeDialog = true
    },
    clickFn() {
      localStorage.setItem("shopeData", JSON.stringify(this.submitData));
      const { submitData } = this
      if (submitData.showMode === 0) {
        this.reultData.afterPrice = this.resultMoney(submitData.cost).afterPrice
        this.reultData.beforePrice = this.resultMoney(submitData.cost).beforePrice
        this.reultData.resProfits = this.resultMoney(submitData.cost).afterPrice * submitData.profit / 100
        this.reultData.resClearProfits = submitData.clearProfit * submitData.exchange / this.resultMoney(submitData.cost).afterPrice * 100
        this.reultData.income = (submitData.cost + submitData.freight) * submitData.exchange + this.reultData.resProfits
      }
      if (submitData.showMode === 1) {
        const tmpArr = []
        const tmpLength = (submitData.maxCost - submitData.minCost) / submitData.step
        let startNum = submitData.minCost
        for (let i = 0; i < tmpLength; i++) {
          tmpArr.push(startNum)
          startNum = startNum + submitData.step
        }
        tmpArr.push(submitData.maxCost)
        const forArr = tmpArr.map(v => {
          v = this.resultMoney(v)
          return v
        })
        this.forArr = forArr
      }

    },
    resultMoney(cost) {
      const { submitData } = this
      // (成本+境内运费) * 汇率 + 50*0.02+15
      let tmpDenominator, tmpMolecule
      if (submitData.checkMode === 0) {
        tmpDenominator = (cost + submitData.freight + submitData.clearProfit) * submitData.exchange + 16
        tmpMolecule = 1 - submitData.service / 100 - submitData.commission / 100 - submitData.activity / 100 - submitData.withdraw / 100
      }
      if (submitData.checkMode === 1) {
        tmpDenominator = (cost + submitData.freight) * submitData.exchange + 16
        tmpMolecule = 1 - submitData.profit / 100 - submitData.service / 100 - submitData.commission / 100 - submitData.activity / 100 - submitData.withdraw / 100
      }
      const data = {
        afterPrice: (tmpDenominator / tmpMolecule).toFixed(2),
        beforePrice: ((tmpDenominator / tmpMolecule) / (100 - submitData.discount) * 100).toFixed(2),
        name: cost
      }
      return data
    }
  }
}
</script>
<style>
body {
  background-color: #f6f6f6;
}
.box {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.wid-100 {
  width: 100%;
}
.flex {
  display: flex;
}
.flex-bet {
  display: flex;
  justify-content: space-between;
}
.check-box {
  width: 475px;
  padding: 20px;
  background-color: #ffffff;
}
.wid-center {
  width: 33%;
  text-align: center;
}
.sub-title {
  display: inline-block;
  width: 25px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  border-radius: 50%;
  margin-bottom: 5px;
  background-color: #ee4d2d;
  color: #fff;
}
.el-form-item {
  margin-bottom: 10px !important;
}

.banner::-webkit-scrollbar {
  width: 6px; /*高宽分别对应横竖滚动条的尺寸*/
  height: 2px;
}
.banner::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 6px;
  background-color: #d2d2d2;
}
.banner::-webkit-scrollbar-thumb:hover {
  background-color: #bababa;
}
.banner::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  border-radius: 6px;
  background: #f6f6f6;
}
</style>


